body{
    background-color:rgb(29, 80, 17);
    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif ;
}
h1,h2,h3,h4,h5,h6,button{
    text-transform: capitalize;
}
h3{
    text-align: start;
}
button.submit-btn{
    padding: 2px 20px;
    box-sizing: border-box;
    border: 1px solid lightseagreen;
    background-color: rgb(0, 225, 255);
    cursor: pointer;
}

section.section-center{
    background-color: white;
    width: 70%;
    height:auto;
    margin: 100px auto;
    padding: 5px 30px 30px 30px;
    box-sizing: border-box;
    text-align: center;
    border-radius: 5px;
}
p.alert {
    margin: 1rem 0 -1rem;
}
p.alert-success {
    background-color: rgb(28, 241, 28);
}
p.alert-danger {
    color: white;
    background-color: rgb(223, 57, 57);
}
article.grocery-item{
    display: flex;
    background-color: whitesmoke;
    justify-content: space-between;
    width: 98%;
    margin: 10px auto;
    padding:0;
}
p.title{
    margin: 0 0 0 15px;
}
button.delete-btn,button.edit-btn {
    border-width: 0px;
}
button.delete-btn{
    color: maroon;
    font-weight: bold;
}
button.edit-btn {
    color: green;

}
button.clear-btn {
    border-width: 0;
    background-color: inherit;
    color: red;
    font-weight: bold;
}